import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';

function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

const MIN = 20;
const MAX = 50;

function makeRandomData(cnt = 200) {
	const data = [];
	let current = 0;
	for (let i = 0, l = cnt; i < l; i += 1) {
		if (!current) {
			current = getRandomInt(MIN, MAX);
		} else {
			current = getRandomInt(current - 5, current + 5);
		}
		data.push({ x: i, y: current })
	}
	return data;
}


const initialState = {
	productionData: makeRandomData(),
	wearData: makeRandomData(),
	windData: makeRandomData(),
	priceData: makeRandomData(),
	demandData: makeRandomData(),
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { productionData, wearData, windData, priceData, demandData } = this.state;
		// const { userStore } = this.props.stores;
		// const { user } = userStore;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Home</h1>

						<div class='d-flex'>
							<div class='p-2 flex-grow-1 bg-light mr-2'>
								Production / Wear
								<LineGraph 
									data={productionData}
									data2={wearData}
									width={600}
									height={300}
									yMax={100}
									y2Max={100}
									showYTicks={1}
									yTicks='["100%", "75%", "50%", "25%", "0%"]'
									showXTicks={1}
								/>
							</div>
							<div class='p-2 bg-light'>Wear status</div>
						</div>

						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								Weather
								<LineGraph 
									data={windData}
									width={300}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["20 m/s", "15 m/s", "10 m/s", "5 m/s", "0 m/s"]'
									showXTicks={1}
								/>
							</div>
							<div class='p-2 flex-fill bg-light mr-2'>
								Electricity prices
								<LineGraph 
									data={priceData}
									width={300}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["2 NOK", "1.5 NOK", "1 NOK", "0.5 NOK", "0 NOK"]'
									showXTicks={1}
								/>
							</div>
							<div class='p-2 flex-fill bg-light'>
								Demand
								<LineGraph 
									data={demandData}
									width={300}
									height={200}
									yMax={100}
									showYTicks={1}
									yTicks='["100 MWh", "75 MWh", "50 MWh", "25 MWh", "0 MWh"]'
									showXTicks={1}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
